
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

#container {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-areas: "sidebar main";
  width: 100vw;
  height: 100vh;
}
#main {
  grid-area: main;
}
#new-message {
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 5px;
  margin-left: 0px;
  border-top: 1px solid #3f3f3f;
}

#new-message input {
  width: 70%;
  border: none;
  outline: none;
  padding: 5px;
}

#messages-list {
  padding: 5px 0 0 5px;
}
#sidebar {
  background-color: #cae7ff;
  grid-area: sidebar;
  padding: 5px 0 0 5px;
  border-right: 1px solid #3f3f3f;
  height: 100%;
}
